import React from 'react';
import * as styles from './styling/promo-card.module.css';
import { BackgroundImageGeneric } from '../generic/BackgroundImageGeneric';
import { ImageGeneric } from '../generic/ImageGeneric';

interface PromoCardProps {
  titleName: string;
  titleImg?: any;
  fluidImg: any;
  clickLink?: string;
  buttonText: string;
  buttonLink: string;
  subtitle?: string;
  secondSubtitle?: string;
  readText?: string;
  readLink?: string;
  mobileOnly?: boolean;
  altText?: string;
}

const PromoCard: React.FC<PromoCardProps> = props => {
  let readMore;
  if (props.readText) {
    readMore = (
      <a href={`${props.readLink}${process.env.GATSBY_PLATFORM_PARAM}`} className={styles.readMore}>
        {props.readText}
      </a>
    );
  }

  let title;
  if (props.titleImg) {
    title = <ImageGeneric className={styles.titleLogo} image={props.titleImg} alt={'Summer Ring Dash Logo'} />;
  } else {
    title = <h2 className={styles.promoTitleBox}>{props.titleName}</h2>;
  }

  const mobileOnlyStyles = props.mobileOnly ? `${styles.mobileOnly} ${styles.box}` : styles.box;

  return (
    <div className={mobileOnlyStyles}>
      <BackgroundImageGeneric className={styles.boxWrapper} img={props.fluidImg} alt={props.altText}>
        <div className={styles.blackOverlay}></div>
        <div className={styles.boxContent}>
          {title}
          <p className={styles.boxSubtitle}>{props.subtitle}</p>
          <p className={styles.boxSubtitle}>{props.secondSubtitle}</p>
          <a href={`${props.buttonLink}${process.env.GATSBY_PLATFORM_PARAM}`} className={styles.btnSmallRed}>
            {props.buttonText}
          </a>
          {readMore}
        </div>
      </BackgroundImageGeneric>
    </div>
  );
};

export default PromoCard;
