import React from 'react';
import {graphql, useStaticQuery} from 'gatsby';
import * as styles from './styling/noPurchase.module.css';
import {BackgroundImageGeneric} from '../generic/BackgroundImageGeneric';

const NoPurchaseNecessary: React.FC<any> = () => {
    const altText = "A phone with Global Poker's poker gameplay on screen next to a set of 5 cards featuring a spades flush." ;
    const images = useStaticQuery(
        graphql`
          query {
            background: file(relativePath: { eq: "index-noPurchase/banner.png" }) {
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
              }
            }
          }
        `
    );

    return (
        <BackgroundImageGeneric img={images.background} className={styles.bannerImage} alt={altText}>
            <div className={styles.container}>
                <div className={styles.textDeclaration}>
                    <h2 className={styles.title}>NO PURCHASE NECESSARY!</h2>
                    <h3 className={styles.text}>
                        It’s always free to play our Sweeps Coins Games!<br/>
                        So far we’ve given our players over 50 million free sweeps coins without any purchases!
                    </h3>
                </div>
            </div>
        </BackgroundImageGeneric>
    );
};

export default NoPurchaseNecessary;
