import React from 'react';
import * as gameStyling from './styling/games.module.css';
import { BackgroundImageGeneric } from '../generic/BackgroundImageGeneric';

interface GameCardProps {
  titleName: string;
  fluidImg: any;
  clickLink: string;
  buttonText: string;
  buttonLink: string;
  subtitle: string;
  readText: string;
  readLink: string;
  altText?: string;
}

const GameCard: React.FC<GameCardProps> = props =>  {
  let readMore;
  if (props.readText) {
    readMore = (
      <a href={props.readLink} className={gameStyling.readMore}>
        {props.readText}
      </a>
    );
  }

  return (
    <div className={gameStyling.box}>
      <BackgroundImageGeneric className={gameStyling.boxWrapper} img={props.fluidImg} alt={props.altText}>
        <div className='dark-overlay'></div>
        <div className={`sections`}>
          <div className={'box-content'}>
            <h3 className={`promo-title-box ${gameStyling.cardTitle}`}>{props.titleName}</h3>
            <p className={`box-subtitle ${gameStyling.cardSubtitle}`}>{props.subtitle}</p>
            <a href={props.buttonLink} className={gameStyling.cardBtn}>
              {props.buttonText}
            </a>
            {readMore}
          </div>
        </div>
      </BackgroundImageGeneric>
    </div>
  );
};

export default GameCard;
