import React from 'react';
import {graphql, useStaticQuery} from 'gatsby';
import * as styles from './styling/getting-started.module.css';
import {BackgroundImageGeneric} from '../generic/BackgroundImageGeneric';

const GettingStartedGuide: React.FC<any> = () => {
    const altText = "A male seated next to his dog, with a laptop on his laptop featuring Global Poker's poker gameplay on screen.";
    const images = useStaticQuery(
        graphql`
      query {
        gettingStarted: file(relativePath: { eq: "getting-started/getting-started-hero.png" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
      }
    `
    );

    return (
        <BackgroundImageGeneric className={styles.heroImage} img={images.gettingStarted} alt={altText}>
            <div className={styles.overlay}></div>
            <div className={styles.container}>
                <h2 className={styles.title}>GETTING STARTED GUIDE</h2>
                <h4 className={styles.subtitle}>
                ALL THE INFORMATION YOU NEED TO PLAY FREE POKER ONLINE AT GLOBAL POKER
                </h4>
                <br/>
                <a className={styles.button} href='/getting-started-verify-redemptions-purchases'>
                    LEARN MORE NOW
                </a>
            </div>
        </BackgroundImageGeneric>
    );
};

export default GettingStartedGuide;
