import React, { ReactChild } from 'react';
import {DefaultBanner} from './DefaultBanner';
import {Carousel} from '../../new-carousel';
import * as styles from './HeroBanner.module.css'

export const HeroBanner = () => {

    const getLatestPromotions = () => {
        const banners: ReactChild[] = [];
        banners.push(<DefaultBanner/>);

        return banners;
    }

    return (
        <Carousel children={getLatestPromotions()} isFullWidth={true} autoPlay={true} className={styles.heroBanner} />
    )
}
