import { graphql, useStaticQuery } from "gatsby";
import React from 'react';
import Banner from '../../banner';
import Button from 'src/components/button';
import { RedCTAButton } from '../../buttons/RedCTAButton';
import * as styles from './styling/DefaultBanner.module.css'

export const DefaultBanner = () => {
    const images = useStaticQuery(
        graphql`
            query {
                desktopBanner: file(relativePath: { eq: "hero/default-desktop-v3.jpg" }) {
                childImageSharp {
                    fluid (maxWidth: 4160) {
                    ...GatsbyImageSharpFluid
                    }
                }
                }
                tabletBanner: file(relativePath: { eq: "hero/default-tablet-v3.jpg" }) {
                childImageSharp {
                    fluid (maxWidth: 1024) {
                    ...GatsbyImageSharpFluid
                    }
                }
                }
                mobileBanner: file(relativePath: { eq: "hero/default-mobile-v3.jpg"  }) {
                childImageSharp {
                    fluid (maxWidth: 490) {
                    ...GatsbyImageSharpFluid
                    }
                }
                }
            }
        `
    );

    const desktopHeroBanner = images.desktopBanner;
    const tabletHeroBanner = images.tabletBanner;
    const mobileHeroBanner = images.mobileBanner;
    const sources = [
        mobileHeroBanner.childImageSharp.fluid,
        {
            ...tabletHeroBanner.childImageSharp.fluid,
            media: `(min-width: 500px)`
        },
        {
            ...desktopHeroBanner.childImageSharp.fluid,
            media: `(min-width: 1024px)`
        }
    ];

    return (
        <Banner sources={sources}>
            <section className={styles.defaultBannerContainer}>
                <div className={styles.defaultBannerContentContainer}>
                    <h1 className={styles.defaultBannerTitle}>THE HOME OF<br/>SOCIAL <br className={styles.brMobile}/>POKER</h1>
                    <Button className={styles.defaultHeroButton} url='/register' label='Play Now' color='red' type='hero'/>
                </div>
            </section>
        </Banner>
    );
}
