import { useLayoutEffect, useState } from 'react'

type IMediaQuery = Array<string>

/*
    Sourced from: https://github.com/jepser/use-match-media
    Allows an array of Media Queries to be run and returns an array of boolean values representing the results

    Sample usage:
         const [isMobile, isDesktop] = useMediaQuery([
            '(max-width: 400px)',
            '(min-width: 800px)'
         ])
 */


export function useMediaQuery (queries: IMediaQuery): Array<boolean> {

    const initialValues = Array(queries.length).fill(false)

    if (typeof window === 'undefined') return initialValues

    const mediaQueryLists = queries.map(query =>
        window.matchMedia(query)
    )
    const getValue = (): Array<boolean> => {
        return mediaQueryLists.map(mediaQuery =>
            mediaQuery.matches
        )
    }

    const [value, setValue] = useState(getValue)

    useLayoutEffect(() => {
        const handleChange = (): void => setValue(getValue)
        mediaQueryLists.forEach(mediaQuery =>
            mediaQuery.addEventListener('change', handleChange)
        )
        return (): void => mediaQueryLists.forEach(mediaQuery =>
            mediaQuery.removeEventListener('change', handleChange)
        )
    }, [])

    return value
}
