import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import PromoCard from './promo-card';

const PromotionBar: React.FC<any> = () => {
  const promoImages = useStaticQuery(
    graphql`
      query {
        poker: file(relativePath: { eq: "poker-games/texas-holdem.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        freePkg: file(relativePath: { eq: "hero/default.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        slots: file(relativePath: { eq: "casino-games/slots/slots-banner.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        jsng: file(relativePath: { eq: "hero/jsng.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        jackpot: file(relativePath: { eq: "jackpot-bg.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
      }
    `
  );

  return (
    <div>
      <section className='sections'>
        <div className='sections-wrapper'>
          <PromoCard
            titleName='Join 250K+ US Poker Players'
            fluidImg={promoImages.poker}
            clickLink={`${process.env.GATSBY_GAMECLIENT_URL}/login`}
            buttonText='Join Now'
            buttonLink={`${process.env.GATSBY_GAMECLIENT_URL}/login`}
            subtitle=''
            secondSubtitle=''
            readText=''
            readLink=''
            altText='A hand holding an ace pair hand of cards surrounded by poker chips.'
          />
          <PromoCard
            titleName='Play Slots Games'
            fluidImg={promoImages.slots}
            clickLink='/casino-games/slots'
            buttonText='Play Now'
            buttonLink='/casino-games/slots'
            subtitle=''
            secondSubtitle=''
            readText=''
            readLink=''
            altText='An angled image of real life classic slot machines.'
          />
          <PromoCard
            titleName='JSNG'
            fluidImg={promoImages.jsng}
            clickLink=''
            buttonText='Play Now'
            buttonLink={`${process.env.GATSBY_GAMECLIENT_URL}/login`}
            subtitle='SC 2,000'
            secondSubtitle='More jackpots, more often!'
            readText=''
            readLink=''
            altText="Global Poker's Jackpot Sit'N'Go Tournaments logo featuring gold coins and cards."
          />

          <PromoCard
            titleName='MILLIONS IN GOLD COINS GUARANTEED'
            fluidImg={promoImages.jackpot}
            buttonText='Play Now'
            buttonLink={`${process.env.GATSBY_GAMECLIENT_URL}/login`}
            mobileOnly={true}
          />
        </div>
      </section>
    </div>
  );
};

export default PromotionBar;
