import * as React from 'react';
import {ReactChild, useRef, useState, useEffect} from 'react';
import * as styles from './Carousel.module.css';
import {useMediaQuery} from '../../service/MediaQuery';

interface CarouselProps {
    children: ReactChild[];
    className?: string;
    hasArrowIndicators?: boolean;
    isFullWidth?: boolean;
    autoPlay?: boolean;
    hasIndicators?: boolean;
    arrowIndicatorsStyling?: string;
}

export function Carousel({ children, className, hasArrowIndicators, isFullWidth, autoPlay, hasIndicators=true, arrowIndicatorsStyling}: CarouselProps) {
    const containerRef = useRef<HTMLDivElement>(null);

    const [containerWidth, setContainerWidth] = useState<number>();
    const [childWidth, setChildWidth] = useState<number>();

    const itemsPerPage = Math.max(Math.floor((containerWidth ?? 100) / (childWidth ?? 100)), 1);
    const pageCount = Math.max(Math.ceil(children.length / itemsPerPage), 1);

    const [pageIndex, setPageIndex] = useState(0);
    const [isPlaying, setIsPlaying] = useState(true);

    const [isMobile] = useMediaQuery([
        '(max-width: 768px)',
    ])

    useEffect(() => {
        const container = containerRef.current;
        const child = container?.firstElementChild;
        if (!container || !child) return;

        // initial widths
        setContainerWidth(container.clientWidth);
        setChildWidth(child.clientWidth);

        // responsive container width
        const resizeObserver = new ResizeObserver((entries) => {
            const entry = entries[0];
            if (!entry) return;

            setContainerWidth(entry.contentRect.width);
        });

        resizeObserver.observe(container);
        return () => resizeObserver.unobserve(container);
    }, [children])

    function onScroll() {
        const container = containerRef.current;
        if (!container) return;

        const scrollPercentage = container.scrollLeft / (container.scrollWidth - container.offsetWidth);
        const index = Math.round(scrollPercentage * (pageCount - 1));
        if (index !== pageIndex) {
            setPageIndex(index);
        }
    }

    function scrollToPage(index: number) {
        const container = containerRef.current;
        if (!container) return;

        container.scrollTo({
            left: (index / (pageCount - 1)) * (container.scrollWidth - container.offsetWidth),
            behavior: 'smooth',
        });
    }

    function handlePlayPause() {
        setIsPlaying(!isPlaying)
    }

    function handleNext() {
        scrollToPage(pageIndex === children.length - 1 ? 0: pageIndex + 1)
    }

    useEffect(() => {
        let intervalId: any
        if (isPlaying && autoPlay) {
            intervalId = setInterval(handleNext, 6000)
        }

        return () => clearInterval(intervalId)
    }, [isPlaying, pageIndex])

    const carouselItems =
      children.map((item, i) => (
          <div key={i} className={`${styles.item} ${isFullWidth && styles.itemFullWidth}`}>
              {item}
          </div>
      ));

    const showControlPanel = hasArrowIndicators && !isMobile || autoPlay || hasIndicators && !isMobile;

    return (
        <div className={`${styles.carousel} ${className}`}>
            <div ref={containerRef} className={styles.items} onScroll={onScroll}>
                {carouselItems}
            </div>
            { showControlPanel ?
                <div className={styles.indicatorsWrapper}>
                    {!isMobile && hasIndicators &&
                        <div className={styles.indicators}>
                            {[...Array(pageCount).keys()].map((_, i) => (
                                <button
                                    key={i}
                                    className={`${styles.indicator} ${pageIndex === i && styles.active}`}
                                    onClick={() => scrollToPage(i)}
                                />
                            ))}
                        </div>
                    }
                    {autoPlay ?
                        (isPlaying ?
                                <button onClick={handlePlayPause} className={styles.pauseButton}>
                                    <img
                                        src={'/images/favicon/pause-button.svg'}
                                        alt={'pause-button'}

                                    />
                                </button>:
                                <button onClick={handlePlayPause} className={styles.playButton}>
                                    <img
                                        src={'/images/favicon/play-button.svg'}
                                        alt={'play-button'}

                                    />
                                </button>
                        ) :
                        hasArrowIndicators && !isMobile &&
                        <div className={styles.arrowIndicators}>
                            {pageIndex > 0 &&
                                <button
                                    className={`${styles.previousChevron} ${arrowIndicatorsStyling ? arrowIndicatorsStyling : styles.chevron}`}
                                    onClick={() => scrollToPage(pageIndex - 1)}
                                    id='carousel-previous'
                                    aria-label='navigation backward'
                                >
                                <span className={styles.iconWrapper}>
                                    <svg width='20' height='20' version='1.1' xmlns='http://www.w3.org/2000/svg'
                                         viewBox='0 0 69 69'>
                                        <path fill='white'
                                              d='M16.2,32.1L43.9,4.4c1.3-1.3,3.5-1.3,4.8,0l3.2,3.2c1.3,1.3,1.3,3.5,0,4.8L30,34.5l21.9,22c1.3,1.3,1.3,3.5,0,4.8l-3.2,3.2c-1.3,1.3-3.5,1.3-4.8,0L16.2,36.9C14.9,35.6,14.9,33.4,16.2,32.1L16.2,32.1z'/>
                                    </svg>
                                </span>
                                </button>
                            }
                            {pageIndex < pageCount - 1 &&
                                <button
                                    className={`${styles.nextChevron} ${arrowIndicatorsStyling ? arrowIndicatorsStyling : styles.chevron}`}
                                    onClick={() => {
                                        scrollToPage(pageIndex + 1);
                                    }}
                                    id='carousel-next'
                                    aria-label='navigation forward'
                                >
                                <span className={styles.iconWrapper}>
                                    <svg width='20' height='20' viewBox='0 0 69 69'
                                         xmlns='http://www.w3.org/2000/svg'>
                                        <path fill='white'
                                              d='M53.9369 36.9133L26.2997 64.5503C24.9668 65.8832 22.8058 65.8832 21.4731 64.5503L18.2496 61.3269C16.919 59.9962 16.9165 57.8397 18.244 56.5059L40.1469 34.4999L18.244 12.494C16.9165 11.1603 16.919 9.00368 18.2496 7.67305L21.4731 4.44963C22.806 3.11672 24.967 3.11672 26.2997 4.44963L53.9367 32.0866C55.2696 33.4194 55.2696 35.5804 53.9369 36.9133Z'
                                        />
                                    </svg>
                                </span>
                                </button>
                            }
                        </div>
                    }
                </div>
                : null
            }
        </div>
    );
}
