import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import * as styles from './styling/testimonial.module.css';
import { BackgroundImageGeneric } from '../generic/BackgroundImageGeneric';
import TestimonialCards from './TestimonialCards';

const TestimonialBar: React.FC<any> = () => {
  const images = useStaticQuery(
    graphql`
      query {
        testimonialBackground: file(relativePath: { eq: "index-testimonials/testimonial-background-banner.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
      }
    `
  );
  return (
    <BackgroundImageGeneric img={images.testimonialBackground} className={styles.bannerImage}>
      <div className={styles.container}>
        <div className={styles.textDeclaration}>
          <h2 className={styles.title}>HEAR WHAT OUR ONLINE COMMUNITY OF POKER PLAYERS HAVE TO SAY</h2>
          <div className={styles.text}>
            We listen to our poker players, we luv poker players feedback! Read some of the things our players have said about Global Poker.
          </div>
        </div>
        <TestimonialCards />
      </div>
    </BackgroundImageGeneric>
  );
};

export default TestimonialBar;
