import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import * as gameStyles from './styling/games.module.css';
import GameCard from './game-card';

const Games: React.FC<any> = () => {
  return (
    <section className='steps'>
      <div className={`hero ${gameStyles.hero}`}>
        <div className={`basic-container ${gameStyles.titleContainer}`}>
          <h2 className={`hero-title ${gameStyles.titleText}`}>THE ULTIMATE POKER EXPERIENCE FOR ONLINE PLAY</h2>
          <h3 className={`hero-subtitle ${gameStyles.subtitleText}`}>
            OUR GOAL IS SIMPLE: TO MAKE GLOBAL POKER THE BEST POKER EXPERIENCE IN THE WORLD!
          </h3>
          <p className={`hero-subtitle ${gameStyles.paragraphText}`}><i>Join the <a className={`card-btn ${gameStyles.cardBtn}`}href={'/?from=register?platform=globalpoker.com'} target={'#'}>Global Poker Club</a> for free right now.</i></p>
        </div>
        <div className={gameStyles.heroRight}>
          <GameBar />
        </div>
      </div>
    </section>
  );
};

export default Games;

const GameBar: React.FC<any> = ({ data }) => {
  const gameImages = useStaticQuery(
    graphql`
      query {
        texas: file(relativePath: { eq: "poker-games/texas-holdem.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        omaha: file(relativePath: { eq: "poker-games/omaha.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
        sitngo: file(relativePath: { eq: "poker-games/jackpot-sng.jpg" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
          }
        }
      }
    `
  );
  return (
    <div>
      <section className={gameStyles.sections}>
        <div className={'sections-wrapper'}>
          <GameCard
            titleName='Texas Hold‘em Poker'
            fluidImg={gameImages.texas}
            clickLink={`/poker-games-online/texas-holdem`}
            buttonText='Play Now'
            buttonLink={`/poker-games-online/texas-holdem`}
            subtitle='Texas Hold‘em is the most popular poker game online. Make the best poker hand you can using your 2 hole cards and the 5 community cards.'
            readText=''
            readLink=''
            altText='A hand holding an ace pair hand of cards surrounded by poker chips.'
          />
          <GameCard
            titleName='Omaha Poker'
            fluidImg={gameImages.omaha}
            clickLink='/poker-games-online/omaha'
            buttonText='Play Now'
            buttonLink='/poker-games-online/omaha'
            subtitle='Each poker player is dealt four hole cards in Omaha Poker. Starting with four cards, of which you may use only two as your hole cards in making your final five card hand.'
            readText=''
            readLink=''
            altText='A hand holding an ace pair hand of cards surrounded by poker chips.'
          />
          <GameCard
            titleName='Jackpot Sit‘N‘Go Poker'
            fluidImg={gameImages.sitngo}
            clickLink='/poker-games-online/jackpot-sitngo'
            buttonText='Play Now'
            buttonLink={`/poker-games-online/jackpot-sitngo`}
            subtitle='Play poker online for up to 200x your buy-in! Three-player hyper-turbo games!'
            readText=''
            readLink=''
            altText='Gold coins being sucked into a black hole.'
          />
        </div>
      </section>
    </div>
  );
};
